<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="12">
        <v-card class="my-8">
          <v-row class="text-center" justify="center">
              <div style="margin: 64px; width: 300px; height: 300px">
                <v-row>
                  <v-card :color="stateList[0]" outlined height="100px" width="100px"><div class="giftCard">{{ giftList[0].name }}</div></v-card>
                  <v-card :color="stateList[1]" outlined height="100px" width="100px"><div class="giftCard">{{ giftList[1].name }}</div></v-card>
                  <v-card :color="stateList[2]" outlined height="100px" width="100px"><div class="giftCard">{{ giftList[2].name }}</div></v-card>
                </v-row>
                <v-row>
                  <v-card :color="stateList[7]" outlined height="100px" width="100px"><div class="giftCard">{{ giftList[7].name }}</div></v-card>
                  <v-card outlined height="100px" width="100px">
                    <div class="giftCard">
                      <v-btn style="height: 100%; width: 100%" outlined color="primary" @click="gachaOnce">
                        抽奖<br>一次<br>{{ requiredGold + ' 金币'}}
                      </v-btn>
                    </div>
                  </v-card>
                  <v-card :color="stateList[3]" outlined height="100px" width="100px"><div class="giftCard">{{ giftList[3].name }}</div></v-card>
                </v-row>
                <v-row>
                  <v-card :color="stateList[6]" outlined height="100px" width="100px"><div class="giftCard">{{ giftList[6].name }}</div></v-card>
                  <v-card :color="stateList[5]" outlined height="100px" width="100px"><div class="giftCard">{{ giftList[5].name }}</div></v-card>
                  <v-card :color="stateList[4]" outlined height="100px" width="100px"><div class="giftCard">{{ giftList[4].name }}</div></v-card>
                </v-row>
              </div>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
    <v-row class="text-center">
      <v-col cols="12">
        <v-btn outlined color="primary">{{ "用户：" + username }}</v-btn>
      </v-col>
      <v-col cols="12">
        <v-btn outlined color="primary">{{ "剩余金币：" + userGold }}</v-btn>
      </v-col>
      <v-col cols="12">
        <v-btn outlined color="red" @click="addGold(20)">增加20金币</v-btn>
      </v-col>
      <v-col cols="12">
        <v-btn outlined color="red" @click="configDialog = true">设置</v-btn>
      </v-col>
    </v-row>
    <v-dialog width="500px" v-model="resultDialog">
      <v-card>
        <v-card-title class="text-h5">抽奖结果</v-card-title>
        <v-card-text>{{ '恭喜获得：' + result}}</v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="resultDialog = false">好</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog width="500px" v-model="configDialog">
      <v-card>
        <v-card-title>设置</v-card-title>
        <v-card-text>
          <v-container>
            <v-row v-for="(e, i) in giftList" :key="i">
              <v-col cols="8">
                <v-text-field :label="'奖品 ' + (i + 1) + ' 名称'" outlined v-model="config.giftList[i].name"/>
              </v-col>
              <v-col cols="4">
                <v-text-field :label="'奖品 ' + (i + 1) + ' 数量'" outlined v-model="config.giftList[i].probability"/>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12">
                <v-text-field label="单次抽卡所需金币" outlined v-model="config.gold"/>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="changeConfig">好</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  export default {
    name: 'Gacha',

    data: () => ({
      username: "Loading…",
      userGold: 0,
      requiredGold: 0,
      giftList: [],
      stateList: ['white', 'white', 'white', 'white', 'white', 'white', 'white', 'white'],
      config: {},
      resultDialog: false,
      configDialog: false,
      result: ''
    }),
    mounted() {
      let that = this
      that.$axios({
        url: '/api/getUserdata',
        method: 'GET'
      })
      .then(function (result){
        that.username = result.data.users[0].name
        that.userGold = result.data.users[0].gold
      })
      that.$axios({
        url: '/api/getConfig',
        method: 'GET'
      })
          .then(function (result){
            that.requiredGold = result.data.gold
            that.giftList = result.data.giftList
            that.config = result.data
          })
    },
    methods: {
      addGold: function (count){
        let that = this
        that.$axios({
          url: '/api/addGold',
          method: 'POST',
          data: {gold: count}
        })
        .then(function (result){
          that.userGold += count
          console.log(result)
        })
        .catch(function (err){
          console.log(err)
        })
      },
      showResult: function (gift){
        let that = this
        let steps = [0, 1, 2, 3, 4, 5, 6, 7, 0, 1, 2, 3, 4, 5, 6, 7]
        for(let i = 0; i <= gift; i++){
          steps.push(i)
        }
        console.log(steps)
        for (let i = 0; i < steps.length; i++) {
          setTimeout(() => {
            that.stateList = ['white', 'white', 'white', 'white', 'white', 'white', 'white', 'white']
            that.stateList[steps[i]] = 'primary'
            if (i === steps.length - 1){
              console.log(i)
              console.log(steps[i])
              console.log(that.giftList[steps[i]])
              that.result = that.giftList[steps[i]].name
              that.resultDialog = true
            }
          }, i * 100)
        }
      },
      gachaOnce: function (){
        let that = this
        that.$axios({
          url: '/api/gachaOnce',
          method: 'GET',
        })
        .then(function (result){
          let data = result.data
          if (data.success){
            that.userGold -= that.requiredGold
            that.showResult(data.gift)
          }
          else{
            alert(data.msg)
          }
        })
      },
      changeConfig: function (){
        let that = this
        that.configDialog = false
        that.config.giftList.forEach(function (e){
          if(typeof e.probability !== 'number'){
            e.probability = parseInt(e.probability)
          }
        })
        that.$axios({
          method: 'POST',
          url: '/api/setConfig',
          data: {config: that.config}
        })
        .then(function (){
          that.requiredGold = that.config.gold
          that.giftList = that.config.giftList
        })
      }
    }
  }
</script>

<style>
.giftCard{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 100%;
}
</style>
